<template lang="pug">
.page-profile
    .card.info(@click="jumpToQr")
        my-avatar.avatar
        .center
            .top
                .name 林小明
                .type I尊车
            .score 我的积分：588分
        .right
            van-icon.icon.qr(name="qr")
            van-icon.icon.arrow(name="arrow")

    .row
        .card.service
            .title 我的专属服务
            .phone
                .left
                    .name 赵晓红
                    .num 12333333333
                van-icon.icon(name="phone")
        .card.mycar
            .title 我的爱车
            .car-name 奔驰GLC

    .menu
        van-cell-group
            van-cell(title="我的贷款" is-link :to="{path: '/my/ioan'}")
            van-cell(title="我的车险" is-link value="2018-12-12到期" :to="{path:'/my/car-insurance'}")
            van-cell(title="我的邀请" is-link :to="{path: '/my/invite'}")
            van-cell(title="在线服务" is-link)
            van-cell(title="设置" is-link)
            van-cell(title="加入爱品车" is-link)

</template>

<script>
export default {
    methods: {
        jumpToQr () {
            this.$router.push({
                path: '/my/qr'
            });
        }
    }
};
</script>

<style lang="scss" scoped>
.page-profile {
    padding: 1rem;
    .card {
        background-color: #f5f5f5;
        padding: 1rem;
        display: flex;
        align-items: center;
        margin-bottom: 1rem;
        &.info {
            .avatar {
                margin-right: 0.5rem;
                flex-shrink: 0;
            }
            .center {
                flex-grow: 1;
                .top {
                    display: flex;
                    align-items: center;
                    .type {
                        color: #f04c03;
                        font-size: 0.87rem;
                        margin-left: 0.5rem;
                    }
                }
                .score {
                    color: #999;
                    padding-top: 0.5rem;
                    font-size: 0.87rem;
                }
            }
            .right {
                display: flex;
                align-items: center;
                .qr {
                    font-size: 2rem;
                }
                .arrow {
                    margin-left: 0.5rem;
                    color: #999;
                }
            }
        }
        &.service,
        &.mycar {
            width: 9rem;
            flex-wrap: wrap;
            .title {
                width: 100%;
                flex-shrink: 0;
                margin-bottom: 0.5rem;
            }
            .phone {
                height: 2.2rem;
                width: 100%;
                flex-shrink: 0;
                display: flex;
                align-items: center;
                justify-content: space-between;
                color: #666;
                .num {
                    padding-top: 0.3rem;
                    color: #999;
                    font-size: 0.9rem;
                }
                .icon {
                    color: #f04c03;
                    font-size: 1.2rem;
                }
            }
            .car-name {
                height: 2.2rem;
                line-height: 2.2rem;
            }
        }
    }
    .row {
        display: flex;
        align-items: stretch;
        justify-content: space-between;
    }
}
</style>



